import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserLoginService } from '../shared/services/user-login.service';
import { SharedService } from 'app/shared/services/SharedService.service';

@Component({
  selector: 'login-form',
  templateUrl: './login-form.component.html',
  styleUrls: ['./login-form.component.scss']
})
export class LoginFormComponent implements OnInit {

  constructor(private router:Router,private userlogin:UserLoginService,
    private _SharedService:SharedService) {

   
   }

  ngOnInit() {
  }
  //link to register
  redirectToRegister(){
    this.router.navigate(['Register']);
  }

  loginUser(e){
    //prevent submit defult behavior 
    e.preventDefault();

    //get form data
    var userName=e.target.elements[0].value;
    var Pass=e.target.elements[1].value;

    //If the user exists in DB USERNAME & USERPASSWORD - return user userid
    this.userlogin.GetUserIdFromUserNameAndPassword({USERID:0,USERNAME:userName,USERPASSWORD:Pass})
    .subscribe(res=>{
     // console.log(res)
     // this._SharedService.currUser.emit(res);
      this.router.navigate(['Clusters',{UserId:res} ]);
    });
 

 
    return false;
  }
}



