import { Component, OnInit, HostListener, Input, ChangeDetectionStrategy } from '@angular/core';
import { CLUSTER } from 'app/shared/classes/cluster';
import { ActivatedRoute, Router } from '@angular/router'
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatStepperModule } from '@angular/material/stepper';
import { Observable } from 'rxjs/Observable';
import { MatMenuModule } from '@angular/material/menu';
import { ClustersService } from 'app/shared/services/clusters.service';
import { SharedService } from 'app/shared/services/SharedService.service';
import { CurrUserSessionCluster } from 'app/shared/classes/CurrUserSessionCluster';
import { debug } from 'util';
@Component({
  selector: 'clusters-page',
  templateUrl: './clusters-page.component.html',
  styleUrls: ['./clusters-page.component.scss']
})
export class ClustersPageComponent implements OnInit {

  sessionId: number;

  currCluster: CLUSTER;

  //----------------RenameClusterDialog---------------------//
  public RenameDialogOpened = false;
  RenameDialogOpen(Cluster) {
    this.currCluster = Cluster
    console.log(this.currCluster)
    this.RenameDialogOpened = true;
  }
  public RenameDialogClose() {
    this.RenameDialogOpened = false;
  }
  public RenameDialogSubmit(clusterTextBox, event) {
    this.RenameDialogClose();
    this.CLUSTERS = this._ClustersServicee.UpdateClusterName(clusterTextBox.value, this.currCluster);
  }
  //----------------end RenameClusterDialog---------------------//
  constructor(private _ClustersServicee: ClustersService, private _SharedService: SharedService,
    private router: Router) {
    this._SharedService.currSession.subscribe((res: any) => {
      console.log("value", res)
      this.sessionId = res
    })
    //constructor call one to get getSession from db
    // _StartPageService.getSession(1);
  }
  @Input() CLUSTERS: Observable<CLUSTER[]>;
  selectedClustersIds: number[] = [];
  //todo:deselect   cluster - change image to prev image and 
  //remove clusterid from this.selectedClustersIds arr
  Select(_CLUSTER: CLUSTER) {
    _CLUSTER.CLUSTER_Image = "assets/images/header/logo_world_2.png"
    this.selectedClustersIds.push(_CLUSTER.CLUSTERID)
    console.log(_CLUSTER, this.selectedClustersIds)

  }

  DeleteCluster(Cluster) {
    this.CLUSTERS = this._ClustersServicee.deleteClusterById(Cluster.CLUSTERID);
  }



  ngOnInit() {
    this.CLUSTERS = this._ClustersServicee.getClusertsWithoutParent();
  }

  StartEdit() {
    this._ClustersServicee.AddClustersParentSessionId(this.sessionId,
      this.selectedClustersIds).subscribe(res => {
        console.log(res);
        this.router.navigate(['dashboard', { currSession: this.sessionId }]);
      })
  }

}



