import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserLoginService } from '../shared/services/user-login.service';
import { SharedService } from 'app/shared/services/SharedService.service';
import { UserRegisterService } from '../shared/services/user-register.service';
@Component({
  selector: 'register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {

  constructor(private router:Router,private _UserRegisterService:UserRegisterService,
    private _SharedService:SharedService) {

   
   }

  ngOnInit() {
  }

  RegisterUser(e){
    //prevent submit defult behavior 
    e.preventDefault();

    //get form data
    var userName=e.target.elements[0].value;
    var Pass=e.target.elements[1].value;

    this._UserRegisterService.RegisterUser({USERID:0,USERNAME:userName,USERPASSWORD:Pass})
    .subscribe(res=>{
      //console.log(res)
      //this._SharedService.currUser.emit(res);
      this.router.navigate(['Clusters',{UserId:res} ]);
    });
 

    return false;
  }

}



