import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Http ,Response, RequestOptions} from '@angular/http';


import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';

import 'rxjs/add/operator/map';
import 'rxjs/add/Observable/throw';
import { Session } from 'app/shared/classes/Session';
import { URLSearchParams,  RequestOptionsArgs, RequestMethod, Headers } from '@angular/http'
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { Subject } from 'rxjs/Subject';
import { user } from 'app/shared/classes/user';
@Injectable()
export class UserRegisterService {

  private isUserLogedIn;
  private userName;
  constructor(private _httpService: Http) { 
    this.isUserLogedIn=false;
  }

  RegisterUser(_user:user):Observable<number>{
    let data = new URLSearchParams();

    let headers = new Headers({ 'Content-Type': 'application/json; charset=utf-8' });
    let options = new RequestOptions({ headers: headers });

    return this._httpService.post('http://localhost:60985/api/Register', 
    JSON.stringify(_user), options )
    .map((response: Response) => {this.isUserLogedIn=false; return <number>response.json();})
    .catch((e: any) => Observable.throw(this._errorHandler(e)));

  }
  
  _errorHandler(error: Response) {
    console.error(error);//console error
    return Observable.throw(error || "server Error");//throw exeption to parent component

  }
}



